import {Component, OnInit, ViewChild} from "@angular/core";
import {GeneralConfigData} from "@component/general-settings/general-config.data";
import {GeneralSettingsComponent} from "@component/general-settings/general-settings.component";
import {VsanGeneralConfigService} from "@component/general-settings/vsan-general-config.service";
import {ConfigureClusterService} from "@generated/configure-cluster-service";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {LoaderName} from "@service/flow/loader-name";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {StatusChangeType, VsanStatusChangeData} from "@service/status-updates/vsan-status-change.data";
import {VsanStatusService} from "@service/status-updates/vsan-status.service";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * Shown instead of the Configure vSAN wizard in case the cluster has no hosts.
 */
@Component({
   templateUrl: "enable-services.html",
   styleUrls: ["enable-services.scss"],
   selector: "vsan-configure-empty-cluster",
})
export class EnableServicesComponent implements OnInit {

   @ViewChild(GeneralSettingsComponent)
   generalSettingsComponent: GeneralSettingsComponent;

   config: GeneralConfigData;
   vsanConfigSpec: VsanConfigSpec;

   clusterRef: ManagedObject;

   @LoadingIndicator({name: LoaderName.ON_INIT})
   loading: boolean;

   @LoadingIndicator({name: LoaderName.ON_CONFIRM})
   saving: boolean;

   errors: string[] = [];

   constructor(private configService: ConfigureClusterService,
         private vsanConfigService: VsanGeneralConfigService,
         private vsanStatusService: VsanStatusService) {
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.cluster.configure.wizard.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      this.clusterRef = ManagedObject.contextObject;
      this.config = await this.vsanConfigService.getVsanGeneralConfigData(this.clusterRef);
      this.vsanConfigSpec = this.config.cloneVsanConfigSpec();
   }

   @ErrorHandler({name: LoaderName.ON_INIT})
   private handleInitError(error: string): void {
      this.errors.push(error);
   }

   @Loader({name: LoaderName.ON_CONFIRM})
   async confirm(): Promise<void> {
      const errors: string[] = this.generalSettingsComponent.config.validateConfig(this.vsanConfigSpec);
      if (errors.length > 0) {
         this.errors = errors;
      } else {
         const task: any = await this.configService.configureCluster(this.clusterRef, this.vsanConfigSpec);

         this.vsanStatusService.storeChange(
               new VsanStatusChangeData(task, StatusChangeType.VSAN_SERVICE_ENABLED));
         VsanUiUtils.closeModalDialog(task);
      }
   }

   @ErrorHandler({name: LoaderName.ON_CONFIRM})
   private handleConfirmError(error: string): void {
      this.errors = [error];
   }
}
