import {Component} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate} from "@component/wizard/flow-wizard.component";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {EncryptionPropertyProvider} from "@generated/encryption-property-provider";
import {VsanStretchedClusterConfig} from "@generated/vsan-stretched-cluster-config";
import {VsanDiskMapping} from "@generated/vsan-disk-mapping";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {DisksClaimingConfigInfo} from "@component/diskmgmt/model/disks-claiming-config-info.data";
import {SpaceEfficiencyService} from "@service/space-efficiency-service";
import {ClaimOption} from "@generated/claim-option";
import {VsanClusterType} from "@generated/vsan-cluster-type";
import {DiskMgmtDiskUtil} from "@util/disk-mgmt-disk.util";
import {AutoClaimSummaryData} from "@component/diskmgmt/model/auto-claim-summary.data";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.configure.wizard.readyToComplete.title"),
      null,
      [VsanCapabilityProvider, EncryptionPropertyProvider],
)
@Component({
   templateUrl: "ready-to-complete-page.html",
   styleUrls: ["ready-to-complete-page.scss"],
})
export class ConfigureVsanSummaryPage implements OnValidate {

   VsanClusterType: typeof VsanClusterType = VsanClusterType;
   ClaimOption: typeof ClaimOption = ClaimOption;

   @InputProperty()
   @OutputProperty()
   vsanConfigSpec: VsanConfigSpec;

   @InputProperty()
   configType: VsanClusterType;

   @InputProperty()
   vcCapabilityData: VsanCapabilityData;

   @InputProperty()
   clusterCapabilityData: VsanCapabilityData;

   @InputProperty()
   encryptionPermissions: boolean;

   @InputProperty(InputProperty.required(false))
   disksClaimingConfigInfo: DisksClaimingConfigInfo;

   @InputProperty(InputProperty.required(false))
   autoClaimedDisks: AutoClaimSummaryData;

   @InputProperty(InputProperty.required(false))
   isAutoClaim: boolean;

   @InputProperty(InputProperty.required(false))
   faultDomainNames: string[];

   @InputProperty(InputProperty.required(false))
   preferredName: string;

   @InputProperty(InputProperty.required(false))
   preferredDomains: DomainOrHostData[];

   @InputProperty(InputProperty.required(false))
   secondaryName: string;

   @InputProperty(InputProperty.required(false))
   secondaryDomains: DomainOrHostData[];

   @InputProperty(InputProperty.required(false))
   witnessHostName: string;

   @InputProperty(InputProperty.required(false))
   witnessHost: ManagedObject;

   @InputProperty(InputProperty.required(false))
   witnessHostDiskMapping: VsanDiskMapping;

   @InputProperty(InputProperty.required(false))
   storagePoolDisks: string[];

   constructor(private spaceEfficiencyService: SpaceEfficiencyService) {}

   get configTypeLabel(): string {
      switch (this.configType) {
         case VsanClusterType.SINGLE_SITE_CLUSTER:
         case VsanClusterType.CUSTOM_FD_CLUSTER:
            return VsanUiUtils.getString("vsan.cluster.configure.summary.singleSiteCluster");
         case VsanClusterType.TWO_HOST_VSAN_CLUSTER:
            return VsanUiUtils.getString("vsan.cluster.configure.summary.roboClusterLabel");
         case VsanClusterType.STRETCHED_CLUSTER:
            return VsanUiUtils.getString("vsan.cluster.configure.summary.stretchedClusterLabel");
         case VsanClusterType.COMPUTE_ONLY:
            return VsanUiUtils.getString("vsan.cluster.configure.summary.computeOnly");
         default:
            throw new Error("Unsupported config type detected: " + this.configType);
      }
   }

   get spaceEfficiencyStatus(): string {
      return this.spaceEfficiencyService.toLabel(this.vsanConfigSpec.spaceEfficiencyConfig);
   }

   get vsanMaxStatus(): string {
      return this.getBooleanPropertyLabel(this.vsanConfigSpec.isVsanMaxEnabled);
   }

   get allowReducedRedundancyLabel(): string {
      return this.getBooleanPropertyLabel(this.vsanConfigSpec.allowReducedRedundancy);
   }

   get dataInTransitEncryptionEnabled(): string {
      return this.getBooleanPropertyLabel(this.vsanConfigSpec.enableDataInTransitEncryption);
   }

   get eraseDisksBeforeUseLabel(): string {
      return this.getBooleanPropertyLabel(this.vsanConfigSpec.eraseDisksBeforeUse);
   }

   get largeScaleClusterSupportLabel(): string {
       return this.getBooleanPropertyLabel(this.vsanConfigSpec.advancedOptions &&
             this.vsanConfigSpec.advancedOptions.largeClusterSupportEnabled);
   }

   get rdmaLabel(): string {
      return this.getBooleanPropertyLabel(this.vsanConfigSpec.enableRdma);
   }

   getBooleanPropertyLabel(property: boolean): string {
      return VsanUiUtils.getBooleanPropertyLabel(property);
   }

   hasNewClaimedCapacity(claimOption: ClaimOption): boolean {
      if (!this.disksClaimingConfigInfo) {
         return false;
      }

      return this.disksClaimingConfigInfo.hasNewClaimedCapacity(claimOption);
   }

   /**
    * Get managed by vSAN claim options with new claimed capacity
    */
   get managedClaimOptions(): ClaimOption[] {
      // Include vSAN Max auto claim option, where disksClaimingConfigInfo is not populated
      if (this.vsanConfigSpec.isVsanMaxEnabled && this.isAutoClaim) {
         return [ClaimOption.SingleTier];
      } else if (!this.disksClaimingConfigInfo) {
         return [];
      }

      return DiskMgmtDiskUtil.MANAGED_BY_VSAN_CLAIM_OPTIONS.filter(
            claimOption => this.disksClaimingConfigInfo.hasNewClaimedCapacity(claimOption));
   }

   totalClaimedCapacity(claimOption: ClaimOption): string {
      if (claimOption === ClaimOption.SingleTier && this.isAutoClaim) {
         return VsanUiUtils.formatFileSize(this.autoClaimedDisks?.totalClaimedCapacity);
      } else if (!this.disksClaimingConfigInfo) {
         return VsanUiUtils.formatFileSize(0);
      } else if (this.disksClaimingConfigInfo.getOldClaimedCapacity(claimOption) === 0) {
         return VsanUiUtils.formatFileSize(this.disksClaimingConfigInfo.getClaimedCapacity(claimOption));
      } else {
         return VsanUiUtils.getString(
               "vsan.cluster.configure.summary.totalAndOldCapacityFormat",
               VsanUiUtils.formatFileSize(this.disksClaimingConfigInfo.getClaimedCapacity(claimOption)),
               VsanUiUtils.formatFileSize(this.disksClaimingConfigInfo.getOldClaimedCapacity(claimOption)));
      }
   }

   vsanMaxTotalClaimedDisks(): string {
      if (this.isAutoClaim) {
         return VsanUiUtils.getString("vsan.cluster.configure.summary.vsanMaxDatastore.claimedDisks.value",
               this.autoClaimedDisks?.disksCount,
               VsanUiUtils.getString("vsan.cluster.configure.summary.vsanMaxDatastore.claimedDisks.auto.label"));
      }

      return VsanUiUtils.getString("vsan.cluster.configure.summary.vsanMaxDatastore.claimedDisks.value",
            this.disksClaimingConfigInfo.getClaimedDiskCount(ClaimOption.SingleTier),
            VsanUiUtils.getString("vsan.cluster.configure.summary.vsanMaxDatastore.claimedDisks.manual.label"));
   }

   getClaimOptionLabel(claimOption: ClaimOption): string {
      switch (claimOption) {
         case ClaimOption.ClaimForStorage:
            return VsanUiUtils.getString("vsan.cluster.configure.summary.totalCapacity");
         case ClaimOption.ClaimForCache:
            return VsanUiUtils.getString("vsan.cluster.configure.summary.totalCache");
         case ClaimOption.SingleTier:
            return VsanUiUtils.getString("vsan.cluster.configure.summary.vsanMaxDatastore.capacitySize.label");
         default:
            return VsanUiUtils.getString("vsan.cluster.configure.summary.capacity",
                  DiskMgmtDiskUtil.getLocalizedStorage(claimOption));
      }
   }

   get faultDomainsConfigString(): string {
      if (this.configType === VsanClusterType.TWO_HOST_VSAN_CLUSTER) {
         return VsanUiUtils.getString("vsan.cluster.configure.summary.configureRoboClusterLabel");
      } else if (this.configType === VsanClusterType.STRETCHED_CLUSTER) {
         return VsanUiUtils.getString("vsan.cluster.configure.summary.configureStretchedClusterLabel");
      } else if (this.configType === VsanClusterType.SINGLE_SITE_CLUSTER
            || VsanUiUtils.isEmpty(this.faultDomainNames)) {
         return VsanUiUtils.getString("vsan.cluster.configure.summary.fdAndWitnessNoConfigLabel");
      } else {
         return VsanUiUtils.getString("vsan.cluster.configure.summary.configureFdsLabel");
      }
   }

   validate = (): string[] => {
      // If stretched or ROBO cluster then prepare the stretch cluster spec
      if (this.isStretchedConfig) {
         this.vsanConfigSpec.stretchedClusterConfig =
               VsanStretchedClusterConfig.Factory.create(
                     this.witnessHost,
                     this.witnessHostDiskMapping,
                     this.storagePoolDisks,
                     this.preferredName,
                     this.getHosts(this.preferredDomains),
                     this.secondaryName,
                     this.getHosts(this.secondaryDomains),
                     true);
      }
      // nothing to validate
      return null;
   };

   get isStretchedConfig(): boolean {
      return this.configType === VsanClusterType.TWO_HOST_VSAN_CLUSTER
            || this.configType === VsanClusterType.STRETCHED_CLUSTER;
   }

   private getHosts(domainOrHostData: DomainOrHostData[]): ManagedObject[] {
      const result: ManagedObject[] = [];

      for (const domainOrHost of domainOrHostData) {
         if (domainOrHost.isHost) {
            result.push(ManagedObject.fromUid(domainOrHost.uid));
         } else {
            for (const host of domainOrHost.children) {
               result.push(ManagedObject.fromUid(host.uid));
            }
         }
      }
      return result;
   }
}
