import {Component, OnInit} from "@angular/core";
import {GeneralConfigData} from "@component/general-settings/general-config.data";
import {VsanGeneralConfigService} from "@component/general-settings/vsan-general-config.service";
import {PageSet} from "@component/wizard/flow-wizard.component";
import {ClaimDisksWitnessHostPage} from "@component/wizard/shared-pages/claim-disks-witness-host-page.component";
import {FaultDomainsPageComponent} from "@component/wizard/shared-pages/fault-domains-page.component";
import {SingleSiteFaultDomainsPage} from "@component/wizard/shared-pages/single-site-fault-domains-page.component";
import {WitnessHostPageComponent} from "@component/wizard/shared-pages/witness-host-page.component";
import {ConfigureClusterService} from "@generated/configure-cluster-service";
import {ConfigureStretchedClusterService} from "@generated/configure-stretched-cluster-service";
import {VsanClusterType} from "@generated/vsan-cluster-type";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {ManagedObject} from "@service/managed-object";
import {StatusChangeType, VsanStatusChangeData} from "@service/status-updates/vsan-status-change.data";
import {VsanStatusService} from "@service/status-updates/vsan-status.service";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {WitnessHostUtil} from "@util/witness-host.util";
import {ClaimDisksPage} from "./claim-disks-page.component";
import {ConfigTypePage} from "./config-type-page.component";
import {ConfigureVsanSummaryPage} from "./ready-to-complete-page.component";
import {ServicesPage} from "./services-page.component";
import {VsanMaxPage} from "./vsan-max-page.component";

@Component({
   templateUrl: "configure-wizard.html",
   selector: "vsan-configure-wizard",
})
export class ConfigureWizardComponent implements OnInit {

   static readonly DEFAULT_CONFIG_TYPE: VsanClusterType = VsanClusterType.SINGLE_SITE_CLUSTER;

   /**
    * Holds wizard context data, shared between all the pages.
    */
   context: any;
   readonly services: any[] = [ConfigureClusterService, ConfigureStretchedClusterService, VsanStretchedClusterService];
   private clusterRef: ManagedObject;
   private generalConfigData: GeneralConfigData;

   errorMessage: string;

   constructor(private dataService: DataServiceClient,
         private vsanConfigService: VsanGeneralConfigService,
         private vsanStatusService: VsanStatusService) {
   }

   async ngOnInit(): Promise<void> {
      this.clusterRef = ManagedObject.contextObject;

      const vsanConfigPromise: Promise<GeneralConfigData> = this.vsanConfigService
            .getVsanGeneralConfigData(this.clusterRef);

      const dataServicePromise: Promise<Map<string, any>> = this.dataService.getProperties([
         ClusterComputeResource.name,
         ClusterComputeResource.host.length,
      ], this.clusterRef);

      let dsProperties: Map<string, any>;
      try {
         [this.generalConfigData, dsProperties] = await Promise.all([vsanConfigPromise, dataServicePromise]);
         this.setWizardContext(dsProperties);
      } catch (e) {
         Logger.error("Unable to get vSAN cluster's configuration and related data service properties: " + e);
         this.errorMessage = VsanUiUtils.getString("vsan.common.error.dataExtraction");
      }
   }

   private setWizardContext = (properties: Map<string, any>) => {
      const clusterName: string = properties.get(ClusterComputeResource.name);
      let hostCount: number = properties.get(ClusterComputeResource.host.length);
      if (!hostCount) {
         hostCount = 0;
      }
      this.context = {
         moRef: this.clusterRef,
         clusterRef: this.clusterRef,
         clusterName: clusterName,
         configType: ConfigureWizardComponent.DEFAULT_CONFIG_TYPE,
         hasDiskGroupsOnWitness: null,
         hostsInCluster: hostCount,
         vsanGeneralConfig: this.generalConfigData,
         isVsanMaxWorkflow: false, // Init here, since the vSAN max page is not always added.
      };
   };

   private includeClaimDisksPage = (configType: VsanClusterType,
         hasDiskGroupsOnWitness: boolean,
         isWitnessDeployedFromOvf: boolean,
         isVsanMaxWorkflow: boolean): boolean => {
      return WitnessHostUtil.isClaimDisksPageVisible(
            configType, hasDiskGroupsOnWitness, isWitnessDeployedFromOvf, isVsanMaxWorkflow);
   };

   readonly pages: PageSet[] = [
      new PageSet([ConfigTypePage]),
      new PageSet([VsanMaxPage],
            (vsanGeneralConfig: GeneralConfigData, configType: VsanClusterType) => {
               return configType !== VsanClusterType.COMPUTE_ONLY
                     && vsanGeneralConfig?.clusterCapabilityData.isVsanMaxSingleTierSupported;
            }),
      new PageSet([ServicesPage],
            /*
             * The services page only loads content when capabilities are enabled.
             * If all of them are disabled we better hide it or it will be empty in the wizard.
             */
            (vsanGeneralConfig: GeneralConfigData, configType: VsanClusterType) => {
               return vsanGeneralConfig
                     && !(configType === VsanClusterType.COMPUTE_ONLY)
                     && (vsanGeneralConfig.clusterCapabilityData.isEncryptionSupported
                           || vsanGeneralConfig.clusterCapabilityData.isCompressionOnlySupported
                           || vsanGeneralConfig.clusterCapabilityData.isDeduplicationAndCompressionSupported
                           || vsanGeneralConfig.clusterCapabilityData.isAdvancedClusterOptionsSupported
                           || vsanGeneralConfig.clusterCapabilityData.isRdmaSupported);
            }),
      new PageSet([ClaimDisksPage],
            (configType: VsanClusterType) => !(configType === VsanClusterType.COMPUTE_ONLY)),
      new PageSet([SingleSiteFaultDomainsPage],
            (configType: VsanClusterType) => {
               return configType === VsanClusterType.CUSTOM_FD_CLUSTER;
            }),
      new PageSet([WitnessHostPageComponent],
            (configType: VsanClusterType) => configType === VsanClusterType.TWO_HOST_VSAN_CLUSTER),
      new PageSet([FaultDomainsPageComponent, WitnessHostPageComponent],
            (configType: VsanClusterType) => configType === VsanClusterType.STRETCHED_CLUSTER),
      new PageSet([ClaimDisksWitnessHostPage], this.includeClaimDisksPage),
      new PageSet([ConfigureVsanSummaryPage]),
   ];

   onConfigureFinish(taskRef: ManagedObject): void {
      this.vsanStatusService.storeChange(new VsanStatusChangeData(taskRef, StatusChangeType.VSAN_SERVICE_ENABLED));
      VsanUiUtils.closeModalDialog();
   }
}
