import {Component, OnInit} from "@angular/core";
import {AutoClaimSummaryData} from "@component/diskmgmt/model/auto-claim-summary.data";
import {DisksClaimingConfigInfo} from "@component/diskmgmt/model/disks-claiming-config-info.data";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {PageItem} from "@component/wizard/page-item";
import {DiskManagementService} from "@generated/disk-management-service";
import {SpaceEfficiencyConfig} from "@generated/space-efficiency-config";
import {VsanClaimDisksData} from "@generated/vsan-claim-disks-data";
import {VsanClaimHostDiskMappingsSpec} from "@generated/vsan-claim-host-disk-mappings-spec";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {DiskMgmtUtil} from "@util/disk-mgmt.util";
import {VsanUiUtils} from "@util/vsan-util";

@WizardPage(
      VsanUiUtils.getString("vsan.configure.claimDisksPage.title"),
      VsanUiUtils.getString("vsan.configure.claimDisksPage.description"),
      [DiskManagementService],
)
@Component({
   templateUrl: "claim-disks-page.html",
   styleUrls: ["claim-disks-page.scss"],
})
export class ClaimDisksPage implements OnInit, OnValidate {

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   @OutputProperty()
   vsanConfigSpec: VsanConfigSpec;

   private _hostsClaimingData: Map<string, VsanClaimDisksData>;

   get hostsClaimingData(): Map<string, VsanClaimDisksData> {
      return this._hostsClaimingData;
   }

   @InputProperty()
   set hostsClaimingData(value: Map<string, VsanClaimDisksData>) {
      this._hostsClaimingData = new Map<string, VsanClaimDisksData>(Object.entries(value));
   }

   @OutputProperty()
   disksClaimingConfigInfo: DisksClaimingConfigInfo;

   @OutputProperty()
   autoClaimedDisks: AutoClaimSummaryData;

   @OutputProperty()
   isAutoClaim: boolean = false;

   dataEfficiency: SpaceEfficiencyConfig;
   isConfigAcceptable: boolean;
   isAutoClaimAllowed: boolean = false;

   // Set to "true" when disk data for manual claiming is collected
   private isManualClaimDiskDataLoaded: boolean = false;

   ngOnInit(): void {
      this.dataEfficiency = this.vsanConfigSpec.spaceEfficiencyConfig;
      // Set auto claim ON for vsan MAX, when there are compatible disks
      this.isAutoClaimAllowed = DiskMgmtUtil.hasVsanMaxCompatibleDisks([...this.hostsClaimingData?.values()]);
      this.isAutoClaim = this.vsanConfigSpec.isVsanMaxEnabled && this.isAutoClaimAllowed;
      // Disable the next button, when manual claim mode page is loaded initially
      // as it loads some more data upon initialization.
      if (!this.isAutoClaim) {
         this.disableNextButton();
      }
   }

   validate(): string[] {
      if (!this.isConfigAcceptable) {
         return [VsanUiUtils.getString("vsan.dialog.semiAutoClaimDialog.confError.notAcceptableErrors")];
      }

      return null;
   }

   onConfigChange(diskMappings: VsanClaimHostDiskMappingsSpec[]): void {
      this.vsanConfigSpec.diskMappings = diskMappings;
      this.pageControls.rebuildToc();
      (this.pageControls as PageItem).nextButtonIsDisabled = false;
      this.isManualClaimDiskDataLoaded = true;
   }

   onIsAutoClaimChange(): void {
      this.vsanConfigSpec.isDiskClaimVsanManaged = this.isAutoClaim;
      // Disable Next button on initial switch between auto/manual claim if the manual claim disk data is not yet
      // collected.
      if (!this.isManualClaimDiskDataLoaded && !this.isAutoClaim) {
         this.disableNextButton();
      }
   }

   onDisksClaimingConfigChange(disksClaimingConfig: DisksClaimingConfigInfo): void {
      this.disksClaimingConfigInfo = disksClaimingConfig;
   }

   onAutoClaimedDisksChange(autoClaimedDisks: AutoClaimSummaryData): void {
      this.autoClaimedDisks = autoClaimedDisks;
   }

   private disableNextButton(): void {
      (this.pageControls as PageItem).nextButtonIsDisabled = true;
   }
}
