import {Component, OnInit} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanHealthStatus} from "@generated/vsan-health-status";
import {Loader} from "@service/flow/loader.decorator";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {ConfigureClusterService} from "@generated/configure-cluster-service";
import {ManagedObject} from "@service/managed-object";
import {PageControls} from "@component/wizard/flow-wizard.component";
import {PageItem} from "@component/wizard/page-item";
import {AlertType} from "@component/validation/alert-type";
import {IllustratedMessageType} from "@component/illustrated-message/illustrated-message.component";
import {VsanHealthUtil} from "@util/vsan-health-util";
import {VsanPrecheckHealthData} from "@generated/vsan-precheck-health-data";
import {AccordionPanelToggleExpandEventArg} from "@directive/clr-accordion/clr-accordion-panel.directive";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.configure.wizard.vsanMax.title"),
      "",
)
@Component({
   templateUrl: "vsan-max-page.html",
   styleUrls: ["vsan-max-page.scss"],
})
export class VsanMaxPage implements OnInit {

   @InputProperty()
   clusterRef: ManagedObject;

   @InputProperty()
   pageControls: PageControls;

   // Use separate outputProperty and assign the value to vsanConfigSpec on Services page,
   // because it is created from scratch there and any values assigned here would be otherwise lost.
   @OutputProperty()
   isVsanMaxWorkflow: boolean;

   @LoadingIndicator()
   loading: boolean;

   @ErrorMessage()
   alertMessage: string;

   vsanMaxPrecheckData: VsanPrecheckHealthData;

   selectedTestId: string;

   VsanHealthStatus: typeof VsanHealthStatus = VsanHealthStatus;
   IllustratedMessageType: typeof IllustratedMessageType = IllustratedMessageType;
   AlertType: typeof AlertType = AlertType;

   constructor(private configService: ConfigureClusterService) {
   }

   async ngOnInit(): Promise<void> {
      (this.pageControls as PageItem).nextButtonIsDisabled = true;
      await this.fetchVsanMaxPrecheckData();
      (this.pageControls as PageItem).nextButtonIsDisabled = false;
   }

   get vsanMaxAlert(): string {
      if (this.vsanMaxPrecheckData.status === VsanHealthStatus.red) {
         return VsanUiUtils.getString("vsan.cluster.configure.wizard.vsanMax.disabled.warning.ineligible");
      }

      return !this.isVsanMaxWorkflow && this.vsanMaxPrecheckData?.status === VsanHealthStatus.green
            ? VsanUiUtils.getString("vsan.cluster.configure.wizard.vsanMax.disabled.warning.eligible")
            : null;
   }

   @Loader({
      srMessage: "vsan.cluster.configure.wizard.vsanMax.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   private async fetchVsanMaxPrecheckData(): Promise<void> {
      this.vsanMaxPrecheckData = await this.configService.getVsanMaxPrecheckData(this.clusterRef);
      // Initially enable vSAN Max workflow if there are no issues reported
      this.isVsanMaxWorkflow = this.vsanMaxPrecheckData?.status === VsanHealthStatus.green;
      // Preselect the first test
      if (!VsanUiUtils.isEmpty(this.vsanMaxPrecheckData?.unhealthyTests)) {
         this.selectedTestId = this.vsanMaxPrecheckData.unhealthyTests[0].helpId;
      }
   }

   toggleExpand(args: AccordionPanelToggleExpandEventArg<string>): void {
      this.selectedTestId = args.isExpanded ? args.model : null;
   }

   getIconShape(status: VsanHealthStatus): string {
      return VsanHealthUtil.getVsanHealthStatusIconShape(status);
   }

   getIconClass(status: VsanHealthStatus): string {
      return VsanHealthUtil.getVsanHealthStatusIconClass(status);
   }
}
