import {Component, OnInit} from "@angular/core";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {VsanUiUtils} from "@util/vsan-util";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {HostFaultDomainData} from "@generated/host-fault-domain-data";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {VsanClusterType} from "@generated/vsan-cluster-type";
import {GeneralConfigData} from "@component/general-settings/general-config.data";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {ClusterMode} from "@generated/cluster-mode";
import {FaultDomainValidationService} from "@service/fault-domain-validation.service";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.configure.wizard.configurationType.title"),
      VsanUiUtils.getString("vsan.cluster.configure.wizard.configurationType.description"),
)
@Component({
   templateUrl: "config-type-page.html",
   styleUrls: ["config-type-page.scss"],
})
export class ConfigTypePage implements OnValidate, OnInit {

   VsanClusterType: typeof VsanClusterType = VsanClusterType;
   proceedWarning: string;
   proceedToNextPage: () => void;

   // Input

   @InputProperty()
   hostsInCluster: number;

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   stretchClusterSupported: boolean;

   @InputProperty()
   clusterHostFaultDomainData: HostFaultDomainData[];

   @InputProperty()
   vsanGeneralConfig: GeneralConfigData;

   // Output

   @OutputProperty()
   vsanConfigSpec: VsanConfigSpec;

   @InputProperty()
   @OutputProperty()
   configType: VsanClusterType;

   @OutputProperty()
   preferredName: string;

   @OutputProperty()
   preferredDomains: DomainOrHostData[];

   @OutputProperty()
   secondaryName: string;

   @OutputProperty()
   secondaryDomains: DomainOrHostData[];

   constructor(private faultDomainValidationService: FaultDomainValidationService) {}

   ngOnInit(): void {
      this.vsanConfigSpec = this.vsanGeneralConfig.cloneVsanConfigSpec();
      this.vsanConfigSpec.clusterMode = null;
   }

   validate(): string[] | Promise<string[]> {
      const validationErrors: string[] = this.getValidationErrors();
      if (validationErrors.length > 0) {
         return validationErrors;
      }

      return null; // proceed directly
   }

   private getValidationErrors(): string[] {
      const validationErrors: string[] = [];

      switch (this.configType) {
         case VsanClusterType.TWO_HOST_VSAN_CLUSTER:
            if (this.hostsInCluster !== 2) {
               validationErrors.push(
                     VsanUiUtils.getString("vsan.generalConfig.wrongHostNumberForRobo", this.hostsInCluster));
            }

            Array.prototype.push.apply(validationErrors,
                  this.faultDomainValidationService.getFaultDomainValidationErrors(this.clusterHostFaultDomainData));
            break;
         case VsanClusterType.STRETCHED_CLUSTER:
            if (!this.stretchClusterSupported) {
               validationErrors.push(VsanUiUtils.getString("vsan.generalConfig.stretchedClusterNotSupported"));
            } else if (this.hostsInCluster < 2) {
               validationErrors.push(VsanUiUtils.getString("vsan.generalConfig.notEnoughHostsForStretchedCluster"));
            }

            Array.prototype.push.apply(validationErrors,
                  this.faultDomainValidationService.getFaultDomainValidationErrors(this.clusterHostFaultDomainData));
            break;
      }
      return validationErrors;
   }

   validateSelection(): void {
      this.pageControls.rebuildToc();
      this.pageControls.clearValidationErrors();

      const errorMessages: string[] = this.getValidationErrors();
      if (errorMessages) {
         this.pageControls.addValidationErrors(errorMessages);
      }

      this.applyDefaultDomains(this.configType === VsanClusterType.TWO_HOST_VSAN_CLUSTER);
   }

   updateClusterType(): void {
      if (this.configType === VsanClusterType.COMPUTE_ONLY) {
         this.vsanConfigSpec.clusterMode = ClusterMode.COMPUTE;
      } else {
         this.vsanConfigSpec.clusterMode = null;
      }

      this.validateSelection();
   }

   private applyDefaultDomains(useDefaults: boolean): void {
      if (useDefaults && this.clusterHostFaultDomainData.length === 2) {
         this.preferredName = VsanUiUtils.getString(
               "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.defaultPreferredName");
         this.preferredDomains = [this.convertDataModel(this.clusterHostFaultDomainData[0])];
         this.secondaryName = VsanUiUtils.getString(
               "vsan.faultDomains.stretchedCluster.wizard.configure.page.faultDomains.defaultSecondaryName");
         this.secondaryDomains = [this.convertDataModel(this.clusterHostFaultDomainData[1])];
      } else {
         this.preferredName = null;
         this.preferredDomains = null;
         this.secondaryName = null;
         this.secondaryDomains = null;
      }
   }

   private convertDataModel(source: HostFaultDomainData): DomainOrHostData {
      return DomainOrHostData.Factory.create(
            source.hostUid,
            source.name,
            true,
            source.primaryIconId,
            false,
            false,
            null,
            null);
   }
}
