import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {InputProperty, OutputProperty, WizardPage} from "@component/wizard/flow-metadata";
import {OnValidate, PageControls} from "@component/wizard/flow-wizard.component";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {EncryptionPropertyProvider} from "@generated/encryption-property-provider";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {GeneralConfigData} from "@component/general-settings/general-config.data";
import {SpaceEfficiencyConfig} from "@generated/space-efficiency-config";
import {SpaceEfficiencyService} from "@service/space-efficiency-service";
import {VsanClusterType} from "@generated/vsan-cluster-type";

@WizardPage(
      VsanUiUtils.getString("vsan.cluster.configure.wizard.services.title"),
      VsanUiUtils.getString("vsan.cluster.configure.wizard.services.description"),
      [VsanCapabilityProvider, EncryptionPropertyProvider],
)
@Component({
   templateUrl: "services-page.html",
})
export class ServicesPage implements OnInit, OnValidate {

   VsanClusterType: typeof VsanClusterType = VsanClusterType;

   @InputProperty()
   vsanGeneralConfig: GeneralConfigData;

   @InputProperty()
   configType: VsanClusterType;

   @InputProperty()
   hasHybridDiskGroups: boolean;

   @InputProperty()
   pageControls: PageControls;

   @InputProperty()
   clusterRef: ManagedObject;

   @InputProperty()
   isVsanMaxWorkflow: boolean;

   @InputProperty()
   @OutputProperty()
   vsanConfigSpec: VsanConfigSpec;

   constructor(private spaceEfficiencyService: SpaceEfficiencyService) {
   }

   ngOnInit(): void {
      // ensure RDMA is always disabled for Configure vSAN wizard
      if (this.vsanGeneralConfig.clusterCapabilityData.isRdmaSupported) {
         this.vsanConfigSpec.enableRdma = false;
      }
   }

   updateVsanConfig(config: VsanConfigSpec) {
      this.vsanConfigSpec = VsanUiUtils.deepClone(config);
   }

   validate(): string[] {
      const validationErrors: string[] = this.vsanGeneralConfig.validateConfig(this.vsanConfigSpec);

      const spaceEfficiency: SpaceEfficiencyConfig = this.vsanConfigSpec.spaceEfficiencyConfig;
      if (this.spaceEfficiencyService.isOnlyAllFlashSupportedForClaimingDisks(spaceEfficiency)
            && this.hasHybridDiskGroups) {
         validationErrors.push(
               VsanUiUtils.getString("vsan.generalConfig.hybridGroupsExist.warning",
                     this.spaceEfficiencyService.toLabel(spaceEfficiency)));
      }

      // If there are validation errors dismiss the disk wiping warning as errors are more important.
      return validationErrors.length > 0 ? validationErrors : null;
   }
}
